<template>
	<div class="detailBanner">
		<div class="detailBg" @click="showImgFunc">
			<img :src="bannerImg"/>
		</div>
		<div class="detailTitle">
			<p>{{sightName}}</p>
			<p>{{gallaryImgs.length}}</p>
		</div>
		<Fade>
			<Album v-show='showImg' @close='closeImg' :gallaryImgs=gallaryImgs></Album>
		</Fade>
	</div>
</template>

<script>
	import Album from '@/common/album/Album.vue'
	import Fade from '@/common/fade/Fade.vue'
	export default{
		data(){
			return{
				showImg:false
			}
		},
		props:['bannerImg','sightName','gallaryImgs'],
		mounted(){
			console.log(222)
		},
		methods:{
			showImgFunc(){
				this.showImg=true
			},
			closeImg(){
				this.showImg=false
			}
		},
		components:{Fade,Album}
	}
</script>

<style scoped>
	.detailBanner{
		position: relative;
	}
	.detailBg{width: 100%;height: 0;padding-bottom: 55%;}
	.detailBg img{width: 100%;}
	.detailTitle{position: absolute;bottom: 0;left: 0;display: flex;width: 100%;}
	.detailTitle p{font-size: 1.6rem;line-height: 3rem;color: #fff;}
	.detailTitle p:first-child{flex: 1;text-indent: 1em;}
	.detailTitle p:last-child{flex: 0 50px;}
</style>


