<template>
	<div class="detail">
		<DetailBanner :bannerImg=bannerImg :sightName=sightName :gallaryImgs=gallaryImgs></DetailBanner>
		<DetailHeader></DetailHeader>
		<DetailContent :categoryList=categoryList></DetailContent>
	</div>
</template>

<script>
	import DetailHeader from 'pages/detail/components/DetailHeader.vue'
	import DetailBanner from 'pages/detail/components/DetailBanner.vue'
	import DetailContent from 'pages/detail/components/DetailContent.vue'
	import Api from '@/api/axios.js'
	export default {
		data(){
			return{
				id:'',
				sightName:'',
				bannerImg:'',
				categoryList:'',
				gallaryImgs:''
			}
		},
		components:{
			DetailBanner,DetailContent,DetailHeader
		},
		methods:{
			getDetail(){
				Api('static/mock/detail.json?id='+this.id).then((msg)=>{
					let datas=msg.data.data
					this.sightName=datas.sightName
					this.bannerImg=datas.bannerImg
					this.categoryList=datas.categoryList
					this.gallaryImgs=datas.gallaryImgs
				})
				.catch((err)=>{
					console.log(err)
				})
			}
		},
		mounted(){
			let id=this.$route.params.id
			this.id=id
			this.getDetail()
		}
	}

</script>

<style>

</style>


