<template>
  <div class="list" ref='list'>
  	<div class="listWrap">

			<div class="area">
				<p>当前城市</p>
				<ul class="cityList">
					<li><div>{{this.getCityName}}</div></li>
				</ul>
			</div>
			<div class="area">
				<p>热门城市</p>
				<ul class="cityList">
					<li v-for="(item,index) in hotCity" :key=index
						  @click="setCity(item.name)"
					>
						 <div>{{item.name}}</div>
					</li>
				</ul>
			</div>
			<div class="areaList">
				<ul class="cityList">
					<li v-for="(items,index) in cities" :key=index :ref=index>
						<p>{{index}}</p>
						<ul class="cityTwo">
							<li v-for="item in items" @click="setCity(item.name)">{{item.name}}</li>
						</ul>
					</li>

				</ul>
			</div>

  	</div>
  </div>
</template>

<script>
import {mapGetters,mapMutations} from 'vuex'
import Bscroll from 'better-scroll'
export default {
  name: 'list',
  data () {
    return {

    }
  },
  props:['hotCity','cities','numChild'],
  mounted(){
  	this.scroll=new Bscroll(this.$refs.list)
  },
  watch:{
  	numChild(){
			if(this.numChild){
				console.log(this.numChild)
				this.scroll.scrollToElement(this.$refs[this.numChild][0])
			}
  	}
  },
  methods:{
  	setCity(name){
  		localStorage.city=name
		this.setcityname(name)
		this.$router.push('/')
  	},
  	...mapMutations({
  		'setcityname':'setcityname'
  	})
  },
  computed:{
  	...mapGetters(['getCityName'])
  }
}
</script>
<style type="text/css" scoped>
	.list{position: absolute;top:88px;bottom: 0;left: 0;right: 0;overflow: hidden;}
.cityList{padding: 1rem 4rem 1rem 1rem;background: #fff;overflow: hidden;}
.cityList li{padding: .5rem;width: 33.33%;float:left;text-align: center;font-size: 1.6rem;}
.cityList li>div{padding: .5rem;border:1px solid #ccc;border-radius: .6rem;}
.areaList li{width:100%;text-align:left;padding:1rem 0;}
.areaList .cityTwo li{border-bottom: 1px solid #eee;text-indent:1em}
.area p{padding: .5rem 1.5rem;color: #666;background: #eee;}
.areaList .cityList{padding: 0;}
.areaList p{background: #eee;padding: .5rem 1.5rem;}
</style>



