<template>
	<div class="grid">
		<mt-swipe :auto="0" >
		  <mt-swipe-item>
			<div class="mui-content">
			        <ul class="mui-table-view mui-grid-view mui-grid-9">
			            <li class="mui-table-view-cell mui-media mui-col-xs-3 mui-col-sm-3" v-for="(gird,key) in iconList" v-if='key < 8'>
			            	<a href="#">
			                    <span class="mui-icon">
			                    	<img :src="gird.imgUrl"/>
			                    </span>
			                    <div class="mui-media-body">{{gird.desc}}</div>
			            	</a>
			            </li>

			        </ul>
			</div>

		</mt-swipe-item>
		<mt-swipe-item v-if='iconList.length>8' >
			<div class="mui-content">
			        <ul class="mui-table-view mui-grid-view mui-grid-9">
			            <li class="mui-table-view-cell mui-media mui-col-xs-3 mui-col-sm-3" v-for="(gird,key) in iconList" v-if='key >=8'>
			            	<a href="#">
			                    <span class="mui-icon">
			                    	<img :src="gird.imgUrl"/>
			                    </span>
			                    <div class="mui-media-body">{{gird.desc}}</div>
			            	</a>
			            </li>

			        </ul>
			</div>
		</mt-swipe-item>
		</mt-swipe>
	</div>

</template>

<script>
export default {
  data(){
    return{
    }
  },
  props:['iconList']
}
</script>

<style lang="stylus" >
	.grid .mui-icon{
		width: 55px;height: 55px;display: inline-block;
	}
	.grid .mui-icon img{
		width: 100%;
	}
	.mui-grid-view.mui-grid-9{
		background: #fff;border: none;
	}
	.mint-swipe-item{background: #fff;}
	.mui-grid-view.mui-grid-9 .mui-table-view-cell{border: none;padding: 0;}
	.mui-content > .mui-table-view:first-child{margin: 0;}
	.mint-swipe .mint-swipe-items-wrap{
		height: 230px!important;
		overflow: hidden;
	}
	.mint-swipe-indicator.is-active{
		background: red;
	}
	.mint-swipe-indicator{
		opacity: .5;
	}
</style>


