<template>
  <div class="city">
  	<CityHeader></CityHeader>
  	<Search></Search>
  	<List :hotCity=hotCity :cities=cities :numChild=numChild></List>
  	<ListOrder :cities=cities @num=numfun></ListOrder>
  </div>
</template>

<script>
	import Search from 'pages/city/components/search.vue'
	import CityHeader from 'pages/city/components/cityHeader.vue'
	import List from 'pages/city/components/list.vue'
	import ListOrder from 'pages/city/components/listOrder.vue'
	import Api from 'api/axios.js'
export default {
  name: 'City',
  data () {
    return {
			hotCity:'',
			cities:'',
			numChild:''
    }
  },
  components:{
  	Search,CityHeader,List,ListOrder
  },
  methods:{
  	getCitys(){
  		Api('static/mock/city.json').then((msg)=>{
  			let datas=msg.data.data
  			this.hotCity=datas.hotCities
  			this.cities=datas.cities
  		})
  		.catch((err)=>{
  			console.log(err)
  		})
  	},
  	numfun(n){
//		console.log(n)
  		this.numChild=n
  	}
  },
  mounted(){
  	this.getCitys()
  }
}
</script>
<style type="text/css" scoped>
	.mint-header{background: #00BCD4;}
	.mint-cell-wrapper{background: #00BCD4;padding: 0 .5rem;}
	.city .mint-cell-value>input{margin-bottom: 0;text-align: center;}
	.city .mint-field-clear{position: absolute;right: 1.5rem;}
</style>



