<template>
	<div class="detailHeader" ref='detailHeader'>
		<router-link to='/'>
			<div class="headerAbs" v-show='absShow'>
				<span class="iconfont icon-Left"></span>
			</div>
		</router-link>

		<div class="headerFixed" v-show='!absShow' :style="opas">
			<div class="cityHeader">
	    	<mt-header title="景点详情">
				  <router-link to="/" slot="left">
					    <mt-button icon="back"></mt-button>
				  </router-link>
			</mt-header>
	    </div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				absShow:true,
				opas:{opacity:.5}
			}
		},
		methods:{
			scrollFunc(e){
				//获取文档的高度
				let top=document.documentElement.scrollTop
				if(top > 50){
					let opa= top/160
					opa = opa > 1 ?1 : opa
					this.absShow=false
					this.opas={ opacity:opa }
				}else{
					this.absShow=true
				}
			}
		},
		mounted(){
			window.addEventListener('scroll',this.scrollFunc)
		},
		unmounted(){
			window.removeEventListener('scroll',this.scrollFunc)
		}
	}
</script>

<style scoped>
.headerAbs{
	position: absolute;
    top: 10px;
    left: 10px;
    background: rgba(0,0,0.5);
    color: #fff;
    border-radius: 50%;
    display: inline-block;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-weight: bold;
}
.headerFixed{position: fixed;top: 0;left: 0;width: 100%;}
.headerFixed .mint-header{background: #00BCD4;}
</style>


