import Vue from 'vue'
import Axios from 'axios'
import qs from 'qs'

//关闭启动是发出生产提示
Vue.config.productionTip = false
//把'/api'设置为vue的全局对象
Vue.prototype.HOST='/api'
//创建axios实例
Axios.defaults.timeout = 10000
//发送前拦截处理格式
Axios.interceptors.request.use(config=>{
	if(config.method === 'post'){
		config.data = qs.stringify(config.data)
	}
	return config
},
	error=>{
		return Promise.reject(error)
	}
)

//请求到数据的拦截器
Axios.interceptors.response.use(msg=>{
	if(msg.status === 200){
		//返回数据
		return msg
	}else{
		//抛出异常
		return Promise.reject('error')
	}
},error=>{
	return Promise.reject(error)
})


export default function axiosApi(url,datas){
//	debugger
//	let data=qs.stringify(datas)
	return new Promise(function(resolve,reject){
		//本地只能get，不能post
		Axios.get(url,datas).then((msg)=>{
			resolve(msg)
		}).catch((msg)=>{
			reject(msg)
		})

	})
}



