<template>
	<div class="header">
		<div class="headerLeft">
			<i class="iconfont icon-Left" style="font-size: 1.6rem;color: #fff;"></i>
		</div>
		<div class="headerCenter">
			<input type="" name="" id="" value="" placeholder="输入城市或者景点"/>
		</div>
		<!--城市路由-->
		<router-link to='/city' tag='div' class='headerRight'>
			<span>{{this.getCityName}}</span><i class="iconfont icon-down" style="font-size: 1.4rem;color: #fff;"></i>
		</router-link>
	</div>

</template>

<script>
	import {mapGetters} from 'vuex'
export default {
  name: 'HomeHeader',
  data () {
    return {
      msg: '我是首页'
    }
  },
  computed:{
  	...mapGetters(['getCityName'])
  }
}
</script>
<style lang="stylus" >
@import "~@/assets/stylus/var.styl"
.header{
	display: flex;
	height: 4rem;
	background: $bgColor;
	color: #fff;
	font-size: 1.6rem
}
.headerLeft{
	flex: 5rem 0;
	text-align: center;
	line-height: 4rem;
}
.headerCenter{
	flex: 1;
	text-align: center;
	line-height: 4rem;
}
.headerCenter input{
	width: 100%;
	height: 2.5rem;
	color: #000;
	text-indent: 1em;
	font-size: 1.2rem
}
.headerRight{
	flex: 7rem 0;
	text-align: center;
	line-height: 4rem;
	font-size: 1.4rem
}
.headerRight>span{display: inline-block!important;}
.headerRight i{margin-left: .5rem;}
</style>


