<template>
		<div class="detailContent">
		<div v-for="item in categoryList">
			<div class="ite">{{item.title}}</div>
			<div v-if='item.children' class="aa">
				<DetailContent :categoryList=item.children></DetailContent>
			</div>
		</div>
	</div>

</template>

<script>
	export default{
		name:'DetailContent',
		props:['categoryList']
	}
</script>

<style scoped>
	.detailContent{background: #fff;}
	.ite{border-bottom: 1px solid #eee;padding:10px 20px;}
	.aa{padding:0 20px;}
</style>


