<template>
  <div class="home">
    <HomeHeader></HomeHeader>
    <HomeSwiper :swiperList=swiperList></HomeSwiper>
    <HomeGrid :iconList=iconList></HomeGrid>
    <HomeHot :recommendList=recommendList></HomeHot>
    <HomeZhoumo :weekendList=weekendList></HomeZhoumo>
  </div>
</template>

<script>
	import Api from 'api/axios.js'
	import HomeHeader from 'pages/home/components/HomeHeader.vue'
	import HomeSwiper from 'pages/home/components/HomeSwiper.vue'
	import HomeGrid from 'pages/home/components/HomeGrid.vue'
	import HomeHot from 'pages/home/components/HomeHot.vue'
	import HomeZhoumo from 'pages/home/components/HomeZhoumo.vue'
export default {
  name: 'Home',
  data () {
    return {
      msg: '我是首页',
      iconList:'',
			recommendList:'',
			swiperList:'',
			weekendList:''
    }
  },
  components:{
  	HomeHeader,
  	HomeSwiper,
  	HomeGrid,
  	HomeHot,
  	HomeZhoumo
  },
  mounted(){
  	//主页大盒子获取数据，分发给儿子们
	this.getHomeInfo();
  },
  methods:{
  	getHomeInfo(){
  		Api('static/mock/index.json',this.msg).then((msg)=>{
  			if(msg.status == '200' && msg.statusText == 'OK'){
  				let datas=msg.data.data
  				this.iconList=datas.iconList
  				this.recommendList=datas.recommendList
  				this.swiperList=datas.swiperList
  				this.weekendList=datas.weekendList
  				console.log(datas)
  			}else{
  				console.log('服务器异常！')
  			}
  		}).catch((err)=>{
  				console.log(err)
  		})
  	}
  }
}
</script>



