<template>
  <div class="search">
			<mt-field placeholder="输入城市名或拼音" v-model="username"></mt-field>
  </div>
</template>

<script>
export default {
  name: 'search',
  data () {
    return {
				username:''
    }
  }
}
</script>
<style type="text/css" scoped>
	.mint-cell-wrapper{background: #00BCD4;padding: 0 .5rem;}
	.search .mint-cell-value>input{margin-bottom: 0;text-align: center;}
	.search .mint-field-clear{position: absolute;right: 1.5rem;}
</style>



