<template>
	<div class="hot">
		<p>热销推荐</p>
		<ul class="mui-table-view">
		    <li class="mui-table-view-cell mui-media" v-for="(item,index) in recommendList" :key=index>
		    	<router-link :to='{path:"/detail/" + item.id}'>
		    		<img class="mui-media-object mui-pull-left" :src="item.imgUrl">
		            <div class="mui-media-body">
		                <span>{{item.title}}</span>
		                <p class='mui-ellipsis'>{{item.desc}}</p>
		                <span href="" class="look">查看详情</span>
		            </div>
		    	</router-link>

		    </li>
		</ul>
	</div>

</template>

<script>
export default {
  data(){
    return{
    }
  },
  props:['recommendList']
}
</script>

<style lang="stylus">
.mui-table-view .mui-media-object{max-width: 9.5rem;height: 9.5rem;}
.look{    line-height: 0.44rem;
    margin-top: 0.16rem;
    background: #ff9300;
    padding: .4rem 1.2rem;
    border-radius: .5rem;
    font-size: 1.5rem;
    color: #fff;}
    .mui-table-view-cell > a:not(.mui-btn){margin: -1.5rem -2.8rem;}
    .hot>p{margin: 0;padding: 1rem;}
    .mui-table-view:before{background: none;}
    .mui-table-view-cell:after{left: 0;}
   .hot .mui-media-body{padding: 10px 0;}
</style>


