<template>
  <div class="listOrder" ref=listOrder>
			<ul ref='listUl'>
				<li v-for="item in orders"
					@touchstart.prevent='touchStart'
					:ref=item
					:class="{pink:item == active}"
				>{{item}}</li>
			</ul>
  </div>
</template>

<script>
export default {
  name: 'listOrder',
  data () {
    return {
				touchStatus:false,
				active:'A'
    }
  },
  props:['cities'],
  methods:{
  	touchStart(e){
  		let num=e.target.innerText
  		this.active=num
  		this.$emit('num',num)
  	}
  },
  computed:{
  	startY(){
  		return this.$refs.listOrder.offsetHeight
  	},
  	listUl(){
  		return this.$refs.listUl.offsetHeight
  	},
  	orders(){
  		let order=[]
  		for(let i in this.cities){
  			order.push(i)
  		}
  		return order
  	}
  }
}
</script>
<style type="text/css" scoped>
	.listOrder{position: absolute;top: 88px;right: 0;bottom: 0;display: flex;justify-content: center;flex-direction: column;line-height: 2rem;}
	.listOrder ul li{color: #00BCD4;padding:0 .8rem;font-size: 1.4rem;text-align: center;}
	.pink{color: pink!important;}
</style>



