<template>
	<div class="swiper">
		<mt-swipe :auto="2000" :speed='0'>
		  <mt-swipe-item v-for="(imgs,index) of swiperList" :key='index'>
		  	<img :src="imgs.imgUrl"/>
		  </mt-swipe-item>
		</mt-swipe>
	</div>

</template>

<script>
export default {
  data(){
    return{
    }
  },
  props:['swiperList']
}
</script>

<style lang="stylus" >
.swiper .mint-swipe .mint-swipe-items-wrap{
	height: 0!important;
	overflow: hidden;
	padding-bottom: 31.5%;
}
.swiper .mint-swipe-item img{
	width: 100%;
	height: 100%;
}
</style>


