<template>
	<div class="Album" @click="closeImg">
		<div class="albumWrap">
			<mt-swipe>
				  <mt-swipe-item v-for="(imgs,index) of gallaryImgs" :key='index'>
				  		<img :src="imgs"/>
				  </mt-swipe-item>
			</mt-swipe>
		</div>
	</div>

</template>

<script>
export default {
  data(){
    return{

    }
  },
  methods:{
  	closeImg(){
		this.$emit('close')
  	}
  },
  props:['gallaryImgs'],
  mounted(){
  	console.log(this.gallaryImgs)
  }
}
</script>

<style>
 .Album{
 	position: fixed;top: 0;left: 0;right: 0;bottom: 0;background: #000;color: #fff;z-index: 999;justify-content: center;flex-direction: column;display: flex;
 }
 .albumWrap{width: 100%;height: 0;padding-bottom: 100%;}
 .albumWrap .mint-swipe{overflow:initial!important;}
 .Album .mint-swipe{padding-bottom: 100%!important;}
 .albumWrap .mint-swipe-items-wrap{padding-bottom: 100%!important;}
 .Album .albumWrap .mint-swipe .mint-swipe-items-wrap{padding-bottom: 100%!important;overflow: initial}
 .mint-swipe-items-wrap img{width: 100%;}
 .albumWrap .mint-swipe-indicators{bottom: 0;width: 100%;text-align: center;background: rgba(0,0,0,.5);}
 .albumWrap .mint-swipe-indicator{background: #fff;opacity: 1;}
 .albumWrap .mint-swipe-indicator.is-active{background: red;}
</style>


